      format("embedded-opentype"),
    url("./fonts/AtlasGrotesk/AtlasGrotesk-Bold.woff2") format("woff2"),
    url("./fonts/AtlasGrotesk/AtlasGrotesk-Bold.woff") format("woff"),
    url("./fonts/AtlasGrotesk/AtlasGrotesk-Bold.ttf") format("truetype"),
    url("./fonts/AtlasGrotesk/AtlasGrotesk-Bold.svg#AtlasGrotesk-Bold")
      format("svg");
  font-weight: bold;
  font-style: normal;
}

